import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { GetService } from '../get.service';

@Component({
  selector: 'app-sign',
  templateUrl: './sign.component.html',
  styleUrls: ['./sign.component.scss']
})
export class SignComponent implements OnInit {

  selectedFile:File=null;
  

  onFileSelect(event){
    this.selectedFile=<File>event.target.files[0]; 
    const fd:any=new FormData();
    fd.append('image',this.selectedFile,this.selectedFile.name);
    localStorage.setItem('image',fd)
  }
  
  constructor(public fb:FormBuilder,public get:GetService,public router:Router) { }

  ngOnInit(): void {
  }
  
  user={
    name:'',
    phone:'',
    password:'',
    repassword:'',
    check:false
  }
  signupForm=this.fb.group({
    name:["",[Validators.required]],
    phone:["",[Validators.required,Validators.pattern('[0-9 ]{10}')]],
    password:['',[Validators.required,Validators.pattern('(?=.*[a-z])(?=.*[0-9])(?=.*[A-Z])[a-zA-Z0-9]{8,}')]],
    repassword:['',[Validators.required]],
    check:['',[Validators.required]]
  });
  verify(){
    if(this.user.check==true){
      if(this.user.password==this.user.repassword){
        this.get.sign(this.user).subscribe((item)=>{
          if(item.msg=='success'){
            alert('Account created successfully');
            this.router.navigate(['']);
          }
          if(item.msg=='fail'){
            alert('This phone number is already registered')
          }
        });
      }
      else{
        alert('Re-enter the password correctly')
      }
     
    }
    else{
      alert('Please accept the terms and conditions')
    }
  }

}
