import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class GetService {
  server_address:string='api';
  constructor(public http:HttpClient) { }
  sign(data:any){
    return this.http.post<any>(`${this.server_address}/sin`,data); 
  }
  log(data:any){
    return this.http.post<any>(`${this.server_address}/log`,data);
  }
  getdata(){
    return this.http.get(`${this.server_address}/get`);
  }
  sendRoom(data:any){
    return this.http.post<any>(`${this.server_address}/room`,data); 
  }

  getToken(){
    return !!localStorage.getItem('token');
  }
  
}
