import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GetService } from '../get.service';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  user={
    phone:'',
    password:''
}
  userId:any;
  constructor(public get:GetService, public router:Router, public fb:FormBuilder) { }
  logForm=this.fb.group({
    phone:["",[Validators.required,Validators.pattern('[0-9 ]{10}')]],
    password:["",[Validators.required]]
})
  ngOnInit(): void {
  }
  
  login(): void {
    this.userId=this.user.phone
    localStorage.setItem('userId',this.userId);
    this.get.log(this.user).subscribe((item)=>{
      if(item.msg=='yes'){
        localStorage.setItem('token',item.token)
       this.router.navigate(['home']);
      }
      else if(item.msg=='invalid'){
        alert('wrong password')
      }
      else{
        alert('No user found with this phone number sign up first')
      }
    });
  }
}
