import { Component, NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { SignComponent } from './sign/sign.component';
import { ChatroomComponent } from './chatroom/chatroom.component';
import { UsereditComponent } from './useredit/useredit.component';
import { VerifyGuard } from './verify.guard';

const routes: Routes = [
  {path:'',component:LoginComponent},
  {path:'home',canActivate:[VerifyGuard],component:HomeComponent},
  {path:'signup',component:SignComponent},
  {path:'chatroom',canActivate:[VerifyGuard],component:ChatroomComponent},
  {path:'edit',canActivate:[VerifyGuard],component:UsereditComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
